.topbar{
    width: 100%;
    height: 50px;
    background-color: white;
    position: sticky;
    top: 0;
    z-index: 9999;

}
.topRight{
    display: flex;
    align-items: center;
}

.topbarwrapper{
    height: 70px;
    padding: 0px  20px;
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.logo{
    font-weight:bold ;
    font-size: 30px;
    color: darkblue;
    cursor: pointer;
}
.topbarIconContainer{
position: relative;
cursor: pointer;
margin-right: 10px;
color: #555;

}
.topIconBadge{
    width: 15px;
    height: 15px;
    position: absolute;
    top:  -5px;
    right:  5px;
    background-color: red;
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}
.topavatar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;

}